import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { EvidenciaPage } from './evidencia.page';

const routes: Routes = [
  {
    path: '',
    component: EvidenciaPage,
  },
  {
    path: 'aceptar-terminos',
    loadChildren: () => import('src/app/pages/SST/aceptar-terminos/aceptar-terminos.module').then(m => m.AceptarTerminosPageModule),
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class EvidenciaPageRoutingModule {}
