import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController, ModalController } from '@ionic/angular';
import { ModalTrabajadoresComponent } from 'src/app/components/modal-trabajadores/modal-trabajadores.component';
import { EDAnexosImagenesPermisoTrabajo } from 'src/app/interfaces/edanexos-imagenes-permiso-trabajo';
import { EDGuardarPermisoTrabajoAPP } from 'src/app/interfaces/edguardar-permiso-trabajo-app';
import { SaveDataService } from 'src/app/services/SST/save-data.service';
import { TablaTrabajadoresService } from 'src/app/services/SST/tabla-trabajadores.service';
import { UtilsService } from 'src/app/services/utils.service';
import { Camera, CameraResultType, CameraSource, GalleryPhotos } from '@capacitor/camera';

@Component({
  selector: 'app-evidencia',
  templateUrl: './evidencia.page.html',
  styleUrls: ['./evidencia.page.scss'],
})
export class EvidenciaPage implements OnInit {
  data: EDGuardarPermisoTrabajoAPP;
  photos: EDAnexosImagenesPermisoTrabajo[] = [
    // {
    //   strNombreImagen: 'Foto 1',
    //   strBase64Image: 'file:///storage/emulated/0/Android/data/io.ionic.starter/cache/1589394485483.jpg',
    // },
    // {
    //   strNombreImagen: 'Foto 1',
    //   strBase64Image: 'file:///storage/emulated/0/Android/data/io.ionic.starter/cache/1589394485483.jpg',
    // },
  ];

  counterPhoto = 0;
  constructor(
    public modalController: ModalController,
    private router: Router,
    private dataService: SaveDataService,
    private alertController: AlertController,
    private utils: UtilsService,
    private tablaTrabajadoresService: TablaTrabajadoresService
  ) {}

  ngOnInit() {
    this.precargarDatos();
  }

  verTrabajadores() {
    this.modalController
      .create({
        component: ModalTrabajadoresComponent,
      })
      .then(modal => {
        modal.present();
      });
  }

  async takePhoto() {
    try {
      const capturePicture = await Camera.getPhoto({
        promptLabelPicture: 'Tomar foto',
        promptLabelPhoto: 'Seleccionar foto',
        promptLabelHeader: 'Seleccionar foto',
        promptLabelCancel: 'Cancelar',
        quality: 80,
        allowEditing: false,
        resultType: CameraResultType.Base64,
        width: 400,
        height: 400,
        source: CameraSource.Camera,
      });

      if (capturePicture == null) {
        return;
      }

      const photo: EDAnexosImagenesPermisoTrabajo = {
        strNombreImagen: 'Foto ' + (this.photos.length + 1),
        strBase64Image: capturePicture.base64String, // URI de la imagen
      };
      this.photos.push(photo);
      this.counterPhoto++;
    } catch (error) {
      console.error('Error al tomar la foto:', error);
      // this.mostrarError(error);
    }
  }

  async selectMultiplePhotos() {
    try {
      await this.utils.presentLoading();

      let fotos = await Camera.pickImages({ quality: 50, height: 400, width: 400, correctOrientation: true });

      if (fotos == null) {
        return;
      }
      await this.addPhotos(fotos);
    } catch (error) {
      console.error('Error al seleccionar las fotos:', error);
      // this.mostrarError(error);
    } finally {
      this.utils.dismissLoading();
    }
  }

  async addPhotos(fotosCargadas: GalleryPhotos) {
    for (let index = 0; index < fotosCargadas.photos.length; index++) {
      const image = fotosCargadas.photos[index];
      try {
        const response = await fetch(image.webPath);
        const blob = await response.blob();

        const reader = new FileReader();

        await new Promise<void>(resolve => {
          reader.onloadend = () => {
            const base64data = reader.result.toString().split(',')[1];
            const photo: EDAnexosImagenesPermisoTrabajo = {
              strNombreImagen: 'Foto ' + (this.counterPhoto + 1), // Aumenta el contador
              strBase64Image: base64data.toString(),
            };
            this.photos.push(photo);
            this.counterPhoto++; // Incrementa el contador
            resolve();
          };
          reader.readAsDataURL(blob);
        });
      } catch (error) {
        console.error('Error al cargar imagen:', error);
      }
    }
  }

  deletePhoto(photo: EDAnexosImagenesPermisoTrabajo) {
    // Elimina la foto del array
    this.photos.splice(this.photos.indexOf(photo), 1);
    if (this.photos.length === 0) {
      this.counterPhoto = 0;
    }
  }

  async continuar() {
    if (this.photos.length === 0) {
      this.mostrarError('Debe agregar al menos una imagen');
      return;
    }
    await this.guardarDatos();
    this.router.navigate([
      '/home/permisos/registro-permiso/crear-permiso/inicio-permiso/seccion-general/seccion-multiusos/seccion-plataforma/descripcion-actividad/proteccion-colectiva/evidencia/aceptar-terminos',
    ]);
  }

  async guardarDatos() {
    this.data['listaAnexoImagenes'] = this.photos;
    this.data['lastRoute'] =
      '/home/permisos/registro-permiso/crear-permiso/inicio-permiso/seccion-general/seccion-multiusos/seccion-plataforma/descripcion-actividad/proteccion-colectiva/evidencia';
    this.dataService.setData(this.data);
    this.dataService.saveData();
  }

  private async mostrarError(message: string) {
    const alert = await this.alertController.create({
      header: 'Alerta',
      message: message,
      buttons: ['OK'],
    });
    alert.present();
  }

  async precargarDatos() {
    const dataUrl = history.state.data;
    console.log(dataUrl);
    await this.utils.presentLoading();
    if (dataUrl) {
      this.tablaTrabajadoresService.setListaTrabajadores(dataUrl['listaTrabajadoresMigrados']);
      this.counterPhoto = dataUrl['listaAnexoImagenes'].length;
      this.photos = dataUrl['listaAnexoImagenes'];
      this.dataService.setData(dataUrl);
      this.data = dataUrl;
    } else {
      this.data = this.dataService.getData();
      this.photos = this.data['listaAnexoImagenes'];
      this.dataService.setData(this.data);
    }
    console.log(this.data);
    this.utils.dismissLoading();
  }

  back() {
    this.router.navigate(
      [
        '/home/permisos/registro-permiso/crear-permiso/inicio-permiso/seccion-general/seccion-multiusos/seccion-plataforma/descripcion-actividad/proteccion-colectiva',
      ],
      { state: { data: this.data } }
    );
  }

  pushPermisosSST(): void {
    this.router.navigate(['/home/permisos']);
  }
}
